/*
SCSS variables are information about icon's compiled state, stored under its original file name

.icon-home {
  width: $icon-home-width;
}

The large array-like variables contain all information about a single icon
$icon-home: x y offset_x offset_y width height total_width total_height image_path;

At the bottom of this section, we provide information about the spritesheet itself
$spritesheet: width height image $spritesheet-sprites;
*/
/*
The provided mixins are intended to be used with the array-like variables

.icon-home {
  @include sprite-width($icon-home);
}

.icon-email {
  @include sprite($icon-email);
}

Example usage in HTML:

`display: block` sprite:
<div class="icon-home"></div>

To change `display` (e.g. `display: inline-block;`), we suggest using a common CSS class:

// CSS
.icon {
  display: inline-block;
}

// HTML
<i class="icon icon-home"></i>
*/
/*
The `sprites` mixin generates identical output to the CSS template
  but can be overridden inside of SCSS

@include sprites($spritesheet-sprites);
*/
.icon-colse {
  background-image: url(../images/sprite.png);
  background-position: -74px 0px;
  width: 58px;
  height: 58px;
  display: inline-block;
  margin-bottom: 5px; }

.icon-right {
  background-image: url(../images/sprite.png);
  background-position: 0px 0px;
  width: 74px;
  height: 58px;
  display: inline-block;
  margin-left: 20px;
  margin-bottom: 5px; }

.prompt-color {
  color: #d83939; }

.loading, .dialog {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8); }

.dialog-content, .prompt-box, .loading_box, .qrcodeanimation, .qrcodesmall {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.md-address h3::before, .list-from li::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: -50%;
  width: 200%;
  height: 1px;
  background-color: #c5c5c5;
  transform: scale(0.5, 0.5); }

.dialog-content.element-active, .prompt-box.element-active {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, -50%) scale(1);
  transition: all 0.2s; }

.dialog-content {
  opacity: 0;
  visibility: hidden;
  width: 80%;
  padding-top: 15px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.3);
  text-align: center;
  transform: translate(-50%, -50%) scale(0);
  transition: all 0.2s; }

.dialog-confirm {
  box-sizing: border-box;
  display: block;
  overflow: hidden;
  padding: 12px 0;
  margin-top: 15px;
  border-top: 1px solid #d5d5d5;
  text-decoration: none;
  color: #006faf; }
  .dialog-confirm:active {
    background-color: rgba(0, 0, 0, 0.1); }

.dialog-content.element-active {
  z-index: 8000; }

.prompt-box {
  opacity: 0;
  visibility: hidden;
  width: 70%;
  padding-bottom: 5px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.1);
  color: #333;
  text-align: center;
  transform: translate(-50%, -50%) scale(0);
  transition: all 0.2s; }
  .prompt-box h3 {
    position: relative;
    height: 45px;
    margin: 0;
    border-radius: 8px 8px 0 0;
    background-color: #fe5a34;
    color: #fff;
