.loading {
  z-index: 9001; }

.loading_box .circle {
  width: 100px;
  height: 100px;
  border: solid 8px;
  border-color: #e74c3c #2980b9 #f1c40f #27ae60;
  border-radius: 100%;
  color: #fff;
  animation: rotate 2s infinite; }

.loading_box .circleLoading {
